@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@300;700&display=swap");

body {
  background-color: #81ae9d;
}
.App {
  font-family: "Nunito", sans-serif;
  width: 270px;
  height: 450px;
  margin: 60px auto;
  background-color: #daeae4;
  border: 5px solid #333333;
  border-radius: 20px;
  border-top: 30px solid #333333;
  border-bottom: 30px solid #333333;
  position: relative;
}
.contact {
  box-sizing: border-box;
  position: relative;
  list-style: none;
  background-color: #fafafa;
  color: #333333;
  padding: 10px 26px;
  left: -17%;
  width: 117%;
  margin: 5px 0;
}
.contact__name {
  margin: 0;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 14px;
}
.contact__phone {
  font-size: 10px;
  color: #888888;
}
.contact__gender {
  position: absolute;
  right: 30px;
}
.App::after {
  content: "";
  width: 10px;
  height: 10px;
  background-color: #fafafa;
  border-radius: 100%;
  position: absolute;
  top: -20px;
  right: 20px;
}
.search {
  display: flex;
  margin: 15px 30px;
  justify-content: space-between;
  font-family: "Nunito", sans-serif;
}
.search__input {
  font-family: "Nunito", sans-serif;
  width: 100%;
  border: #fafafa;
  padding: 5px;
}
.btn {
  font-family: "Nunito", sans-serif;
  width: 30%;
  background-color: #81ae9d;
  color: #fafafa;
  border: 0px solid rgba(0, 0, 0, 0);
  transition: 0.3s ease;
}
.btn:hover {
  background-color: #fafafa;
  color: #333333;
  border: 1px solid #81ae9d;
}
